<template>
	<view class="person_wrap">
		<template v-if="personList.length>0">
			<view class="personList" v-for="(item,index) in personList" :key="index">
				<view class="listTop">
					<view class="listLeft" @click="goDet(item)">
						<view class="nameTag">
							<text class="custname">{{item.custName}}</text>
							<text class="tags" v-if="item.intensionLevelName">{{item.intensionLevelName}}</text>
							<text class="tags bule" v-if="item.stateName">{{item.stateName}}</text>
							<text class="tags red" v-if="pageIndex == 2">{{item.timeRemain > 0 ? item.timeRemain + '天后逾期' : '明天逾期'}}</text>
							<text class="tags red" v-if="pageIndex == 3 && item.timeOverdue">{{'逾期'+item.timeOverdue+'天'}}</text>
						</view>
						<view class="custMobile">{{item.custPhone}}</view>
					</view>
					<image @click="callPhone(item)" src="/static/salePerson/phone1.png" mode=""></image>
				</view>
				<view class="listBottom" @click="goDet(item)">
					<view class="bottomType">
						<text class="typeNameTag">{{item.behaviorName}}</text>
						<text class="contentText">{{item.content}}</text>
					</view>
					<text class="time">{{item.followExcuteTime}}</text>
				</view>
				
				
				
				
				<!-- <view class="listTop">
					<view class="listLeft" @click="goDet(item)">
						<view class="nameTag">
							<text class="custname">{{item.custName}}</text>
							<text class="tags" v-if="item.intensionLevelName">{{item.intensionLevelName}}</text>
							<text class="tags bule" v-if="item.stateName">{{item.stateName}}</text>
							<text class="tags" v-if="pageIndex == 2">{{item.timeRemain > 0 ? item.timeRemain + '天后逾期' : '明天逾期'}}</text>
							<text class="tags" v-if="pageIndex == 3 && item.timeOverdue">{{'逾期'+item.timeOverdue+'天'}}</text>
						</view>
						<view>{{item.custPhone}}</view>
					</view>
					<image @click="callPhone(item)" src="/static/salePerson/phone1.png" mode=""></image>
				</view>
				<view class="listBottom" @click="goDet(item)">
					<text>{{item.followExcuteTime}} {{item.behaviorName}}</text>
					<text class="contentText">{{item.content}}</text>
				</view> -->
			</view>
		</template>
		<template v-else>
			<image class="noData" src="@/static/img/noData.png" mode=""></image>
		</template>
	</view>
</template>

<script>
	export default {
		props: ['isCustomer', 'personList', 'pageIndex'],
		data() {
			return {}
		},
		methods: {
			// 打开电话联系
			callPhone(item) {
				uni.makePhoneCall({
					phoneNumber: item.custPhone
				});
			},
			// 去客户详情
			goDet(item){
				uni.navigateTo({
					url:'/pages/salemanVisual/customer/customerDet?id=' + item.id + '&state=' + item.state
				})
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/personList.scss';
	page {
		height: 100%;
		background-color: #f6f6f6;
	}
</style>
